<script>
// import HotSong from "@/components/HotSong.vue";
import SongList from "@/components/SongList.vue";
import Loading from "@/components/Loading";
export default {
  components: {
    //第三：注册
    // HotSong,
    SongList,
    Loading,
  },
  props: {
    //接收父传来的id
    currentSongId: {
      type: Number,
    },
    playing: Boolean, //接收音频事件，并且还往下传   来实现动画效果
  },
  data: function () {
    return {
      hotsong: [], //歌词为null,一开始时Ajax拿到数据
      formateTime: null, //当前值（时间过滤器）
      time: Date.now(), //初始值（时间过滤器）
      playList: [], //全部歌曲200首
      limit: 20, //当前20首
      hotList: [], //????
      showList: [], //?????
      showLoading: true,
    };
  },
  created: function () {
    this.axios
      .get("https://apis.netstart.cn/music/playlist/detail?id=3778678")
      .then((res) => {
        // console.log(this,res)//拿到数据
        // this.hotsong = res.data.playlist.tracks.slice(0,20); //得到数据上面就遍历显示
        this.playList = res.data.playlist.tracks; //全部歌曲200首
        // console.log(this,playList)//拿到数据

        // //////////////////////////////////////////////////////
        // this.playList = res.data.playlist;
        // //歌单得到的歌曲数据和 首页获取 最新音乐的歌曲信息不一样.
        // //需要处理成 最新音乐数据一致方式
        // this.hotList = res.data.playlist.tracks.map((d, index) => {
        //   //把返回的 tracks里面数据 data.ar 转化为 输出里面 data.song.artists
        //   let data = d;
        //   data.song = {
        //     artists: d.ar,
        //     privilege: {
        //       playMaxbr: res.data.privileges[index].playMaxbr,
        //     },
        //   };
        //   return data;
        // });
        // this.showList = this.hotList.slice(0,this.limit);
        this.hotsong = res.data.playlist.tracks.slice(0, this.limit); //得到数据上面就遍历显示  当前20首
        this.showLoading = false;
        // this.hotList=d.data.playlist.tracks;
      });
  },
  methods: {
    loadeMore() {
      this.hotsong = this.playList.slice(0, this.limit + this.limit);
      // this.hotsong=this.showList,
      // this.showLoading = true;
          // let num = this.showList.length + this.limit;
          // this.showList = this.hotList.slice(0, num);
      this.showLoading = false;
    },
  },
  filters: {
    //使用过滤器实现时间更新
    formateTime: function () {
      var d = new Date();
      // console.log(d);
      // return d.getMonth() + 1 + "月" + d.getDate() + "日";
      var times = d.getMonth() + 1 + "月" + d.getDate() + "日";
      // console.log(s);
      return times;
    },
  },
};
</script>

